
const anch = () => {
  const headerMenu = document.querySelector('.header-menu'),
    footerMenu = document.querySelector('.footer-menu');

  headerMenu.addEventListener('click', event => {
    let target = event.target;
    if (target.classList.contains('header-list-item-link')){
      event.preventDefault();
      document.querySelector('.header-menu').classList.toggle('burger-active');
      const id = target.getAttribute('href');
      const elem = document.querySelector(id);
      window.scroll({
        top: elem.offsetTop,
        behavior: 'smooth'
      });
    }
  });

  footerMenu.addEventListener('click', event => {
    let target = event.target;
    if (target.classList.contains('footer-list-item-link')){
      event.preventDefault();
      const id = target.getAttribute('href');
      const elem = document.querySelector(id);
      window.scroll({
        top: elem.offsetTop,
        behavior: 'smooth'
      });
    }
  });
};


export default anch;